:root {
  --primaryColor: #02075d;
}

body > #root > div {
  height: -webkit-fill-available;
  width: -webkit-fill-available;
}

#H1 {
  color: var(--primaryColor);
  font-size: 16;
  font-weight: 500;
}
.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.BoxCommon {
  align-items: center;
  height: max-content;
  justify-content: center;
  width: max-content;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
